<script>
  import axios from 'axios'
  import {
    get_user_info,
    addPatient,
    checkPatient,
    clickDiagnoseButton,
    editPatientBasicInfo,
    get_patient_info,
    orderPushed,
    removePatient,
    sendSmsFromPlatform,
    manage_click_times,
    change_update_status
  } from '../../api/api';
  import url from "../../static/js/hostUrl";
  import cookie from "../../static/js/cookie";
  import "../../static/js/jquery.form";

  let host = url.url;
  export default {
    name:'undiagnosed',
    /*created：在模板渲染成html前调用，即通常初始化某些属性值，然后再渲染成视图。*/
    created:function(){
      const hosType = localStorage.getItem('type');
      if(hosType==='1'){
        this.superHos = true;
        this.normalHos = false;
      }else{
        this.normalHos = true;
        this.superHos = false;
      }

      let page_info = this.$store.state.page_info;
      let p = page_info['currentPage'];
      let page_size = page_info['page_size'];
      this.currentPage = p;
      this.page_size = page_size;
      this.get_patients(p,page_size);
    },
    /*mounted：在模板渲染成html后调用，通常是初始化页面完成后，再对html的dom节点进行一些需要的操作。*/
    mounted:function(){
      this._get_user_info();
    },

    // watch:{                           /*采用watch方法，监听到hospital_id变化时再执行get_patients方法*/
    //   hospital_id(newVal,oldVal){
    //     console.log('oldVal',oldVal);
    //     console.log('newVal',newVal);
    //     let page_info = this.$store.state.page_info;
    //     let flag = this.$store.state.flag;
    //     if(flag){this.get_patients(1,10);return false};
    //
    //     if(page_info.page_size&&page_info.currentPage){
    //       this.page_size = page_info.page_size;
    //       this.currentPage = page_info.currentPage;
    //       this.get_patients(page_info.currentPage,page_info.page_size);
    //     }else{
    //       this.get_patients(1,10);
    //     }
    //   },
    //   immediate: true
    // },

