      /*组件重新加载*/
      // reloadFun () {
      //   this.reload()
      // },
      /*刷新*/
      refresh:function () {
        $('#selectMenu').html('查看全部');
        this.payment = '';

        let page_info = this.$store.state.page_info;

        //let p = page_info['currentPage'];
        let page_size = page_info['page_size'];
        this.currentPage = 1;
        let p = 1;
        //let page_size = 10;

        this.inputVal = '';
        this.dateValue = '';

        this.tableData = []; /*为了实现刷新效果，将数据栏的数据先清空再加载*/
        this.get_patients(p,page_size,'','');

      },
      selectAll() {
        this.payment = '';
        $('#selectMenu').html('查看全部');
        this.refresh()
      },
      selectPaid(){
        /*筛选已付费的患者*/
        $('#selectMenu').html('查看已付费');
        this.payment = 1;
        let page_size = this.page_size;
        let dateValue = this.dateValue;
        let search = this.inputVal;
        this.get_patients(1,page_size,dateValue,search,1)
      },
      selectUnPaid(){
        /*筛选未付费的患者*/
        $('#selectMenu').html('查看未付费');
        this.payment = 0;
