      //提交
      addSubmit: function () {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              console.log('this.jwsForm',this.jwsForm);
              console.log(this.leftDiopterValue,this.rightDiopterValue);
              let jws= this.jwsForm;
              let submitInfo = {};
              if(this.leftDiopterValue!==''){
                jws['left_diopter'] = this.leftDiopterValue;
              }
              if(this.rightDiopterValue!==''){
                jws['right_diopter'] = this.rightDiopterValue;
              }
              submitInfo['jwsInfo'] = jws;
              let basicInfo = this.addForm;

              delete basicInfo['genderRadio'];
              delete basicInfo['paymentRadio'];
              delete basicInfo['jwsRadio'];
              submitInfo['basicInfo'] = basicInfo;
              let para = Object.assign({}, submitInfo);
              //para['basicInfo']['user_token'] = cookie.getCookie('token');
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              delete basicInfo['jwsVisible'];
              addPatient(para).then((res) => {
                this.addLoading = false;
