      /*点击查看AI报告*/
      async getAIreport(index, row){
        this.editForm = Object.assign({}, row);
        let editInfo = {};
        editInfo['patient_id'] = this.editForm.patient_id;
        editInfo['doctor2'] = localStorage.getItem('user_id');
        this.$store.commit('changePageFrom','undia'); //更新 ‘页面来自哪里’
        await this.transferPinfo(this.editForm);
        this.$router.push('report');
        localStorage.setItem('reportType', '0');
        await this._manage_click_times(editInfo);
