      /*管理查看AI报告按钮的点击数*/
      _manage_click_times:function(data){
        return new Promise((resolve,reject)=>{
          manage_click_times(data).then((res)=>{
            if(res){
              resolve(res);
              console.log(res);
            }else{
              reject()
            }
          })

        })

      },


      //发送短信
      sendSms:function(patient_name, patient_tel){
        let data = {};
        let text_message_amount = this.text_message_amount;
        data['hospital_id'] = localStorage.getItem('hospital');
        data['user_id'] = localStorage.getItem('user_id');
        data['patient_name'] = patient_name;
        data['patient_tel'] = patient_tel;
        if(text_message_amount<=0){
          /*如果分配的短信数量用光，则不允许再发送短信*/
          this.$message({
            message: '您的短信数量已用完，请联系管理员',
            type: 'warning'
          });
          return false;
        }else{
          return new Promise((resolve,reject)=>{
            sendSmsFromPlatform(data).then((res)=>{
              if(res){
                resolve(res)
              }else{
                reject()
              }
            })

          })
        }

      },

