      transferPinfo:function (editForm) {
        let patient_info = {};
        patient_info['page_size'] = this.page_size;
        patient_info['currentPage'] = this.currentPage;
        patient_info['patient_name'] = editForm.patient_name;
        patient_info['patient_gender'] = editForm.patient_gender;
        patient_info['patient_age'] = editForm.patient_age;
        patient_info['patient_id'] = editForm.patient_id;
        patient_info['diagnose_record'] = editForm.diagnose_record;
        patient_info['apply_time'] = editForm.apply_time;
        patient_info['picture'] = editForm.picture;
        patient_info['diagnose'] = editForm.diagnose;
        patient_info['historyDisease'] = editForm.historyDisease;
        patient_info['hospital_id'] = editForm.hospital;

        patient_info['refine_days'] = editForm.refine_days;
        patient_info['refine_weeks'] = editForm.refine_weeks;
        patient_info['birth_days'] = editForm.birth_days;
        patient_info['birth_weeks'] = editForm.birth_weeks;
        patient_info['birth_weight'] = editForm.birth_weight;

        let superHosList = editForm.superior_hospital;
        let id_list = [];
        for(let i=0;i<superHosList.length;i++){
          id_list.push(superHosList[i])
        }
        patient_info['id_list'] = id_list;
        localStorage.setItem('patient_info', JSON.stringify(patient_info));
