      get_file_num :function () {
        let fileReturnValue = [];
        let fileNum = document.getElementById("fileToUpload").files.length;
        let files = document.getElementById("fileToUpload").files;
        for (let i=0;i<files.length;i++){
          console.log('1024',files[i].size);
          if(files[i].size/1048576>15){
            fileReturnValue[0]=false;
            break
          }else{
            fileReturnValue[0]=true;
          }
        }

        fileReturnValue.push(fileNum);

        return fileReturnValue;
      },
      uploadPic:function() {
        // check_expired().then(response=>{
        //   console.log('response',response);
        // });
        const loading = this.$loading({
          lock: true,
          // text: 'Loading',
          text: 'AI智能识别左右眼中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
        });
        setTimeout(() => {
          loading.setText('AI智能计算中');
        }, 3000);

        let num = this.get_file_num()[1];
        let fileSize = this.get_file_num()[0];
        if(num>2){
          this.$alert('单次上传照片数量不可超过两张', '温馨提示', {
            confirmButtonText: '确定',
            callback: action => {
              console.log(action)
            }
          });
          loading.close();
          return false;
        }else if(fileSize===false){
          this.$alert('单张照片大小不可超过10M', '温馨提示', {
            confirmButtonText: '确定',
            callback: action => {
              console.log(action)
            }
          });
          loading.close();
        }else {
          // 上传设置
          let options = {
            // 规定把请求发送到那个URL
            url: host+ "operation/web_upload_picture/",
            data:{'userName':cookie.getCookie('name'),
              'patient_id':this.upload_id,
              'diagnose_record':this.diagnose_record},
            // 请求方式
            type: "post",
            // 服务器响应的数据类型
            dataType:"json",
            cache: false,
            headers:{
              Authorization : localStorage.getItem('token')
            },
            processData: false,   // 告诉jQuery不要处理数据
            contentType: false,   // 告诉jQuery不要设置类型
            // 请求成功时执行的回调函数
            // error:function (result, status, xhr) {
            //    alert("session 过期");
            // }

            success: function(result, status, xhr) {
              console.log('result',result);
              if(result===0){
                loading.close();
                this.$alert('上传失败,仅支持jpg,png,jpeg,bmp格式', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }else if(result===1){
                loading.close();
                this.$alert('上传失败', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }else if(result===2){
                loading.close();
                let flag = true;
                this.$store.commit('changeFlag',flag);
                this.refresh();
                let business_type = localStorage.getItem('business_type');
                if(business_type === '1'){
                  this.sendSms(this.sms_patient_name, this.sms_patient_tel)  //发送短信
                }
                this.$alert('上传成功', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }
              else{
                loading.close();
                this.$alert('上传失败', '温馨提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.log(action)
                  }
                });
              }
            }.bind(this),
            error:function () {
              loading.close();

              this.$alert('上传失败', '温馨提示', {
                confirmButtonText: '确定',
                callback: action => {
                  console.log(action)
                }
              });
            }.bind(this)
          };
          $("#jvForm").ajaxSubmit(options);
        }
      },

    },


    data() {
      let validateTel = (rule, value, callback) => {
        let telLength = value.length;
        if (telLength !==11 ) {
          callback(new Error('手机号为11位'));
        } else {
          callback();
        }
      };

      let validateIDnumber = (rule, value, callback) => {
        if(value){
          let telLength = value.length;
          if (telLength !==18 ) {
            callback(new Error('中国大陆身份证号为18位'));
          } else {
            callback();
          }
        }else{
          callback();
        }
      };

      let validateName = (rule, value, callback) => {
        let nameLength = value.length;
        let reg = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
        if (nameLength >10 ) {
          callback(new Error('姓名长度需要小于10个字'));
        }else if(!reg.test(value)){
          callback(new Error('禁止输入空格等特殊字符'));
        }
        else {
          callback();
        }

      };

      let validateAddress = (rule, value, callback) => {
        if(!value){
          callback();
        }
        let nameLength = value.length;
        if (nameLength >30 ) {
          callback(new Error('地址长度需要小于30个字'));
        } else {
          callback();
        }
      };

      let validateAge = (rule, value, callback) => {
        if (value<1) {
          callback(new Error('请填写正确年龄'));
        } else {
          callback();
        }
      };

      // let validateJws = (rule, value, callback) => {
      //   let jws = this.addForm.jwsExist;
      //   if (jws===1) {
      //       console.log('wqweqwe',this.jwsForm);
      //       callback(new Error('请选择既往史'));
      //
      //   } else {
      //     callback();
      //   }
      // };


      return {
        updateList: ['解决浏览器兼容问题','解决操作栏位排列错乱问题','小程序新增AI收费功能','小程序更新AI报告样式'],
        upload_id : '',
        hospital_id: 0,
        diagnose_record:0,
        superHos:false,
        normalHos:false,
        pregnant_seen:false,
        pre_seen:false,
        page_info:{},
        tableData: [],
        dateValue:'',
        isActive1:true,
        isActive4:false,
        currentPage: 1,
        inputVal:'',
        input:'',
        text_message_amount:0,
        update_info_read: 1,
        sms_patient_name:'',
        sms_patient_tel:'',
        page_size:10,
        totalNum:30,
        otherDate:false,
        pic_upload:false,
        editFormjwsVisible:false,
        radio:3,
        payment:'',
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },

        editFormVisible: false,//编辑界面是否显示
        editLoading: false,
        editFormRules: {
          patient_name: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { validator: validateName, trigger: 'change' }
          ],
          patient_tel: [
            { required: true, message: '请输入手机号', trigger: 'blur' },
            { validator: validateTel, trigger: 'blur' }
          ],
          ID_number: [
            { validator: validateIDnumber, trigger: 'blur' },
            {
              pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
              message: '证件号码格式有误！',
              trigger: 'blur'
            }
          ],
          address:[
            { validator: validateAddress, trigger: 'change' }
          ],
          patient_age: [
            { required: true, message: '请输入正确的年龄', trigger: 'blur' },
            { validator: validateAge, trigger: 'blur' }
          ]
        },
        //编辑界面数据
        editForm: {
          patient_id: 0,
          patient_name: '测试',
          patient_gender: '0',
          patient_age: 0,
          payment:0,
          patient_tel:18888888888,
          ID_number:'',
          birth: '',
          paymentRadio:0,
          jwsRadio:0,
          jwsExist:0,
          jwsVisible:false,
          isFront:true,
          address: '',
          picture:[],
          refine_weeks:0,
          refine_days:0,
          birth_weeks:0,
          birth_days:0,
          birth_weight:0
        },
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {
          patient_name: [
            { required: true, message: '请输入姓名', trigger: 'change' },
            { validator: validateName, trigger: 'change' }
          ],
          patient_tel: [
            { required: true, message: '请输入手机号', trigger: 'change' },
            { validator: validateTel, trigger: 'change' }
          ],
          ID_number:[
            { validator: validateIDnumber, trigger: 'change' },
            {
              pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
              message: '证件号码格式有误！',
              trigger: 'change'
            }
          ],
          jwsExist: [
            { required: true, message: '请选择既往史', trigger: 'change' },
            //{ validator: validateJws, trigger: 'change' }
          ],
          patient_age:[
            { required: true, message: '请输入正确的年龄', trigger: 'change' },
            { validator: validateAge, trigger: 'blur' }
          ],
          address:[
            { validator: validateAddress, trigger: 'change' }
          ],
        },
        //新增界面数据
        addForm: {
          patient_id: 0,
          patient_name: '张三',
          patient_gender: 1,
          patient_age: 0,
          patient_tel: '',
          address: '',
          jwsExist:0,
          jwsVisible:false, //既往史界面是否显示
          jwsRadio:0,
          genderRadio:0,
          ID_number:'',
          refine_weeks:0,
          refine_days:0,
          birth_weeks:0,
          birth_days:0,
          birth_weight:0
        },
        jwsForm:{
          other:'',
          DM_year:false, //糖尿病
          HTN_year:false, //高血压
          HPL_year:false, //高血脂
          pregnant_weeks:'',
          },
        editFormJwsForm:{
          other:'',
          DM_year:false, //糖尿病
          HTN_year:false, //高血压
          HPL_year:false, //高血脂
          pregnant_weeks:'',
        },

        leftDiopter: [{
          value: '0',
          label: '正视'
        },{
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(300~600)'
        }, {
          value: '3',
          label: '高度近视(>600)'
        }, {
          value: '4',
          label: '远视'
        }],
        rightDiopter: [{
          value: '0',
          label: '正视'
        }, {
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(300~600)'
        }, {
          value: '3',
          label: '高度近视(>600)'
        }, {
          value: '4',
          label: '远视'
        }],
        editFormLeftDiopter: [{
          value: '0',
          label: '正视'
        },{
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(300~600)'
        }, {
          value: '3',
          label: '高度近视(>600)'
        }, {
          value: '4',
          label: '远视'
        }],
        editFormRightDiopter: [{
          value: '0',
          label: '正视'
        }, {
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(300~600)'
        }, {
          value: '3',
          label: '高度近视(>600)'
        }, {
          value: '4',
          label: '远视'
        }],
        leftDiopterValue: '',
        rightDiopterValue: '',
        editFormLeftDiopterValue: '',
        editFormRightDiopterValue: '',
      }
    }
  }

</script>

<style scoped>
.undiagnosed{
  text-align: left;
  margin-top: 20px;
  min-width: 1100px;
}
  .timezone,.dataZone{
    margin-top: 20px;
  }
  .el-date,.el-icon-date,.icon-shuaxin1{
    color: #0F9273;
  }

.operationNew{
  margin-top: 20px;
  text-align: center;
  line-height: 41px;
}
  a{
    text-decoration: none;
    outline: none;
    color: #0F9273;
  }

  .el-input__inner{
    border: #0F9273 1px solid;
    color: #0F9273;
  }

  .active{
    border: 1px solid #0F9273;
    border-radius: 4%;
  }


  .pagination{
    margin-top: 20px;
    text-align: right;
  }

.el-date-editor.el-input, .el-date-editor.el-input__inner{
  width: 140px;
}

.timepicker >>> i{
  color: #0F9273;
  font-size: large;
}
  .timepicker >>> input {
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: #0F9273;
  }

.timepicker >>>input::-moz-placeholder{
  color: #0F9273;
}

/*.el-loading-text{*/
/*  font-size: x-large;*/
/*}*/

@media screen and (min-width:600px) { /*大于600px小于1650px*/
  .undiagnosed {
    /*background-color: blue;*/
  }

}

@media screen and (min-width:1650px) { /*大于1650px小于1900px*/
  .undiagnosed {
    /*background-color: red;*/
  }
}

@media screen and (min-width:1900px) {  /*大于1900px*/
  .undiagnosed {
    /*background-color: yellow;*/
  }
}

.el-dropdown {
  vertical-align: top;
}
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.updateTips{
  width: 500px;
  height: 630px;
  position: absolute;
  z-index: 99;
  left:0;
  right:0;
  margin:auto;
  overflow: auto;
  animation:myfirst 2s;
  -webkit-animation:myfirst 2s; /* Safari and Chrome */
}

.updateTips>>>ul{
  position: absolute;
  list-style: none;
  top: 50%;
}
.updateTips>>>li{
  text-indent: 20px;
  padding-bottom: 10px;
  padding-right: 40px;
}

@keyframes myfirst
{
  0%   { left:0;}
  25%  { left:200px; }
}

@-webkit-keyframes myfirst /* Safari and Chrome */
{
  0%   {left:0; }
  25%  {left:200px;}
}
.el-icon-circle-close{
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
}
</style>



